@import "./style/_vars.scss"; @import "./style/_mixins.scss"; @import "./spritesmith-generated/sprite.scss";
.input {
  input {
    text-align: left;
    display: block;
    width: 100%;
    border: 1px solid $light-grey;
    padding: 20px 24px;
    border-radius: 5px;
    @include anim();

    &:hover {
      border-color: $grey;
    }

    &:focus {
      border-color: $dark-grey;
    }

    &.wpcf7-not-valid {
      border-color: $red;
      color: $red;
      @include placeholder($red);
    }
  }

}
